.picker__wrapper {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background: rgb(248, 249, 250);
}

.picker__header {
  text-align: center;
}


.managers__wrapper {
  display: grid;
  grid-template-columns: repeat(1, 400px);
  grid-template-rows: repeat(1, 400px);
  justify-content: center;
}

.managers__buttons {
  display: flex;
  width: 20%;
  justify-content: center;
}

.button {
  width: 80px;
  height: 30px;
  margin: 0 5px;
  background: rgb(200, 45, 45);
  color: #fafafa;
  border-style: none;
  border-radius: 15px;
  transition: .5s;
  cursor: pointer;

  &:hover {
    background: rgb(24, 37, 63);
  }
}

.picker__form {
  display: flex;
  height: 180px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  font-size: 1.2rem;

  .form__input {
    font-size: inherit;
  }
}